.App {
  text-align: center;
}

.bgContent {
  /* background: url('./img/bg.jpg'); */
  width: 1024px;
  margin: auto;
}

.App-header {
  background-color: #222;
  height: 150px;
  /* padding: 20px; */
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.cellContent {
  padding: 10px 0 0 0;
  width: 1024px;
  background-image: url('./img/content-bg-opacity.jpg');
  /* background-size: cover; */
  background-repeat: repeat;
  /* background-position: 50% 50%; */
}

.promiseItem {
  position: relative;
  background: rgba(0,0,0,0.66);
  margin: 30px 100px;
  width: 824px;
  height: 150px;
  /* 阴影 */
  /* box-shadow: 4px 2px 10px #a69d9d; */
  /* border-top-left-radius: 50px;
  border-bottom-right-radius: 50px; */
}

.firstLevel {
  position: relative;
  height: 44px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  margin: 0 24px;
}

.firstLevel span {
  margin-top: 20px;
  line-height: 22px;
  font-size: 16px;
  color: #E6E6E6;
  display: inline-block;
}

.secondLevel {
  position: relative;
  height: 30px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  margin: 0 24px;
}

.secondLevel span {
  line-height: 22px;
  width: 50%;
  font-size: 16px;
  margin-top: 8px;
  color: #E6E6E6;
  display: inline-block;
}

.thirdLevel {
   position: relative;
   height: 28px;
   margin:0 24px;
   text-align: right;
}

.thirdLevel span {
  line-height: 22px;
  display: inline-block;
  margin-top: 6px;
  font-size: 16px;
  color: #E6E6E6;
}

.fourthLevel {
  position: relative;
  height: 30px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  margin: 0 24px;
}

.fourthLevel span {
  width: 100%;
  text-align: right;
  font-size: 16px;
  display: inline-block;
  line-height: 22px;
  margin-top: 8px;
  color: #E6E6E6;
}

