/* 阴影框 */
.addContent {
  position: absolute;
  width: 1024px;
  height: 1100px;
  background-color: rgba(0,0,0,0.4);
  z-index: 1000;
}

.frameBorder {
  position: absolute;
  left: 50%;
  top: 564px;
  width: 700px;
  height: 388px;
  margin: -350px 0 0 -376px;
  background-color: #F2F2F2;
  z-index: 1;
}

.backBtn {
  width: 12px;
  height: 12px;
  margin-left: 600px;
  margin-top: 30px;
}

.nameInfo {
  margin: 30px auto;
}

.submit {
  margin:20px;
  font-size: 16px;
  width: 70px;
  height: 30px;
}

.selectBox {
  width: 60%;
  margin: 30px auto 0 auto;
}

.nameInfo {
  width: 80%;
  height: 40px;
  margin:auto;
  /* background-color: cyan; */
}

.leftInput {
  margin-right: 30px;
}

.nameInfo input {
  width: 30%;
  height: 30px;
  border-radius: 2px;
  border-width: 1px;
  border-color: #d9cfcf;
  border-style: solid;
}



