.headerView {
  text-align: center;
  background: url('../../img/bg.png');
  width: 1024px;
  height: 240px;
  position: relative;
}

.left {
  width: 108px;
  height: 240px;
  /* display: inline-block; */
  float: left;
}

.leftImg {
  width: 108px;
  height: 240px;
}

.center {
  /* display: inline-block; */
  margin-left: 32px;
  width: 522px;
  height: 240px;
  float: left;
}

.headerTitle {
  color: #E6E6E6;
  font-size: 36px;
  width: 522px;
  height: 66px;
  margin-top: 66px;
  display: inline-block;
}

.subSpan {
  color: #E6E6E6;
  font-size: 16px;
  display: inline-block;
  height: 44px;
  width: 500px;
}

.right {
  /* display: inline-block; */
  margin-left: 46px;
  width: 316px;
  height: 240px;
  float: right;
}


.centerBG {
  width: 265px;
  height: 240px;
  margin-right: 51.1px;
}

.rightBG {
  position: absolute;
  right: 0;
  top: 0;
  width: 93px;
  height: 104px;
}



